.app{font-weight:500;max-width:1280px;margin:0 auto}.app h1{font-size:20px;text-align:center;font-weight:bold;margin:20px 0}.app .btn-area{max-width:500px;padding:0 20px;margin:0 auto 10px}.app .btn-area button{width:100%;height:50px;text-align:center;margin-bottom:10px;color:white;font-weight:bold;font-size:18px;border-radius:25px}.app .btn-area button.lawson{background-color:#076cb9}.app .btn-area button.familymart{background-color:#00ab4e}@media (min-width: 768px){.app .btn-area{display:flex;max-width:1024px}.app .btn-area button:not(:last-of-type){margin-right:20px}}@media (min-width: 1024px){.app{grid-template-columns:1fr 1fr 1fr}}
