.food-list{width:calc(100% - 40px);margin:0 auto;display:grid;grid-gap:20px 10px;gap:20px 10px;grid-template-columns:1fr 1fr}.food-list p{font-size:14px;line-height:1.5}.food-list li a,.food-list li img{width:100%}.food-list li a{display:block}.food-list li a:hover img{opacity:0.8}.food-list li .img{position:relative;margin-bottom:8px}.food-list li .img img{transition:opacity 0.2s;position:absolute;top:0;left:0;width:100%;height:100%}.food-list li .date span:first-of-type{margin-right:8px}.food-list li .name{font-weight:bold}.food-list li.LAWSON .img{padding-top:77%}.food-list li.LAWSON .name{color:#076cb9}.food-list li.FamilyMart .img{padding-top:100%}.food-list li.FamilyMart .name{color:#00ab4e}@media (min-width: 768px){.food-list{grid-template-columns:1fr 1fr 1fr}}@media (min-width: 1024px){.food-list{grid-template-columns:1fr 1fr 1fr 1fr}}.MuiPagination-root{display:flex;justify-content:center}.MuiPagination-root.topPagenation{margin-bottom:20px}.MuiPagination-root.bottomPagenation{margin-top:20px}
